/* FONT PATH
 * -------------------------- */
@font-face {
	font-family: 'owfont';
	src: url('../assets/fonts/owfont-regular.eot?v=1.0.0');
	src: url('../assets/fonts/owfont-regular.eot?#iefix&v=1.0.0') format('embedded-opentype'),
		url('../assets/fonts/owfont-regular.woff') format('woff'),
		url('../assets/fonts/owfont-regular.ttf') format('truetype'),
		url('../assets/fonts/owfont-regular.svg#owf-regular') format('svg');
	font-weight: normal;
	font-style: normal;
}
.owf {
	display: inline-block;
	font: normal normal normal 14px/1 owfont;
	font-size: inherit;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	transform: translate(0, 0);
}
/* makes the font 33% larger relative to the icon container */
.owf-lg {
	font-size: 1.33333333em;
	line-height: 0.75em;
	vertical-align: -15%;
}
.owf-2x {
	font-size: 2em;
}
.owf-3x {
	font-size: 3em;
}
.owf-4x {
	font-size: 4em;
}
.owf-5x {
	font-size: 5em;
}
.owf-fw {
	width: 1.28571429em;
	text-align: center;
}
.owf-ul {
	padding-left: 0;
	margin-left: 2.14285714em;
	list-style-type: none;
}
.owf-ul > li {
	position: relative;
}
.owf-li {
	position: absolute;
	left: -2.14285714em;
	width: 2.14285714em;
	top: 0.14285714em;
	text-align: center;
}
.owf-li.owf-lg {
	left: -1.85714286em;
}
.owf-border {
	padding: 0.2em 0.25em 0.15em;
	border: solid 0.08em #eeeeee;
	border-radius: 0.1em;
}
.owf-pull-right {
	float: right;
}
.owf-pull-left {
	float: left;
}
.owf.owf-pull-left {
	margin-right: 0.3em;
}
.owf.owf-pull-right {
	margin-left: 0.3em;
}

